<template>
  <div class="baidu-map">
    <h3></h3>
    <div ref="container" class="bmap"></div> 
    <input type="text" v-model="periphery">
    <input type="button" value="" @click="searchPeriphery">
    <hr>
    <input type="text" v-model="start">
    <input type="text" v-model="end">
    <input type="button" value="" @click="searchLine">
  </div>
</template>
<script>
export default {
  data(){
    return {
      map:null,
      point:null,

      start:' ',
      end:' ',
      periphery:''
    }
  },
  methods:{
    searchPeriphery(){
      this.map.clearOverlays();       
      var local =  new BMap.LocalSearch(this.map, {renderOptions: {map: this.map, autoViewport: false}});  
      local.searchNearby(this.periphery,this.point,1000);

      var marker = new BMap.Marker(this.point);        //  
      this.map.addOverlay(marker);                     //  

    },
    searchLine(){
      this.map.clearOverlays(); 
      var driving = new BMap.DrivingRoute(this.map, {renderOptions:{map: this.map, autoViewport: true},policy: BMAP_DRIVING_POLICY_LEAST_DISTANCE});
			driving.search(this.start,this.end);
    }
  },
  mounted(){
    /* navigator.geolocation.getCurrentPosition(
      function(ev){
        console.log('',ev.coords.longitude) //
        console.log('',ev.coords.latitude) //
      },
      function(error){console.log('error',error)},
    )  */
    this.map = new BMap.Map(this.$refs.container); //  containerid
    this.point = new BMap.Point(121.4978411502, 31.3846399566); //
    this.map.centerAndZoom(this.point, 15);  //
    this.map.enableScrollWheelZoom(true);     //

    // var myIcon = new BMap.Icon("http://lbsyun.baidu.com/jsdemo/img/fox.gif", new BMap.Size(300,157));

    var marker = new BMap.Marker(this.point);        //  
    // var marker = new BMap.Marker(this.point,{icon:myIcon});
    this.map.addOverlay(marker);                     //  
    // marker.setAnimation(BMAP_ANIMATION_BOUNCE); //
  }
}
</script>
<style scoped>
.bmap{height: 300px;}
</style>


